import { nanoid } from "nanoid";
const initialState = [
  {
    id: nanoid(),
    fname: "Sayantan",
    lname: "Chakravarty",
    email: "sayantanchakravarty@gmail.com",
    phone: "1236547890",
    gender: "male",
  },
];
const studentReducer = (state = initialState, action) => {
  switch (action.type) {
    case "ADD_CONTACT": {
      state = [...state, action.payload];
      return state;
    }
    case "DELETE_STUDENT": {
      const filteredStudent = state.filter((student) => {
        return student.id !== action.payload;
      });
      state = filteredStudent;
      return state;
    }
    case "UPDATE_STUDENT": {
      const updatedState = state.map((student) => {
        if (student.id === action.payload.id) {
          return action.payload;
        } else {
          return student;
        }
      });
      state = updatedState;
      return state;
    }
    default:
      return state;
  }
};
export default studentReducer;
